import {Router} from 'aurelia-router';
import routes from './routes';
import $ from 'jquery';

export class App {
  title: string;
  router: Router;

  constructor() {
    this.title = 'Aurelia community';
  }

  configureRouter(config, router) {
    config.title = this.title;
    config.addPipelineStep('modelbind', RouterStep);
    config.addPipelineStep('postcomplete', PostCompleteStep);

    // routes.push({ route: 'login', name: 'login', moduleId: 'users/login', nav: false, title: 'Login' });
    // routes.push({ route: 'logout', name: 'logout', moduleId: 'users/logout', nav: false, title: 'Logout' });
    config.map(routes);
    this.router = router;
    // return this.sharedData.refresh();
  }
}

class RouterStep {
  run(routingContext, next) {
    return next();
  }
}

class PostCompleteStep {
  run(routingContext, next) {
    // $(".page-host").scrollTop(0);
    return next();
  }
}